<template>
  <div>
    <div class="w">
      <el-form ref="content"
               :model="content"
               label-width="80px">
        <el-form-item label="标题">
          <el-input v-model="content.title"></el-input>
        </el-form-item>
        <el-form-item label="内容">
          <el-input type="textarea"
                    v-model="content.data"
                    :autosize="{ minRows: 8, maxRows: 12}"></el-input>
        </el-form-item>
        <div style="text-align:center">
          <el-button type="primary"
                     @click="onSubmit">发表</el-button>
          <el-button>取消</el-button>
        </div>
      </el-form>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
export default {
  data () {
    return {
      content: {
        title: '',
        data: '',
        addTime: '',
        addUser: ''
      }
    }
  },
  computed: {
    ...mapState(['userInfo'])
  },
  methods: {
    async onSubmit () {
      var date = new Date()
      this.content.addTime = date.getFullYear() + '-' + (date.getMonth() + 1) + '-' + date.getDate()
      this.content.addUser = this.userInfo.username
      var res = await this.$http.post('/addContent', this.content)
      if (res.data === 'success') {
        alert('发帖成功')
        this.$router.push('/')
      } else {
        alert('发帖失败')
      }
    }
  }
}
</script>

<style lang="less" scoped>
.w {
  max-width: 1200px;
  margin: 20px auto;
}
</style>
